<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <div class="temp-item-inlineblock temp-item-right">
        <!-- 类型 -->
        <el-select v-model="optionsValue.type" multiple collapse-tags style="margin-left: 20px;"
            placeholder="请选择类型"  >
            <el-option v-for="item in options.type"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <!-- 状态 -->
        <el-select v-model="optionsValue.status" multiple collapse-tags style="margin-left: 20px;"
            placeholder="请选择状态"  >
            <el-option v-for="item in options.status"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <!-- 来源 -->
        <el-select v-model="optionsValue.source" multiple collapse-tags style="margin-left: 20px;"
            placeholder="请选择来源"  >
            <el-option v-for="item in options.source"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
      </div>
      
      <div class="temp-item-inlineblock temp-item-right">
        <el-input v-model="optionsValue.name" clearable placeholder="人群名称" ></el-input>
      </div>
      <div class="temp-item-inlineblock temp-item-left">
        <el-button type="primary" @click="newAudienceVisible = true"><i class="el-icon-plus"></i>创建人群</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-left">
        <el-button type="primary" @click="handleListRemove">删除</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <el-button type="primary" @click="handleListRefresh">刷新</el-button>
      </div>

    </div>
    <div class="table-wrap">
      <el-table v-loading="loading" :data="tableData" @selection-change="handleSelectionChange" style="width: 100%" :default-sort="{prop: 'created_time', order: 'descending'}">
        <el-table-column type="selection">
        </el-table-column>
        <el-table-column prop="label" label="名称" sortable>
        </el-table-column>
        <el-table-column key="type" label="类型" :sort-method="sortTypeChange" sortable>
          <template slot-scope="scope">
            {{audience_type_list[scope.row.type]['name']}}
            <br>
            <span style='font-size:11px;color:#909599;'>{{audience_type_list[scope.row.type]['parent_name']}}</span>
          </template>
        </el-table-column>
        
        <el-table-column key="status" label="状态" :sort-method="sortStatusChange" sortable>
          <template slot-scope="scope">
            <span v-if="scope.row.status=='SUCCESS'">
                <span style="color:#4caf50">●</span>
                {{audience_status_list[scope.row.status]['name']}}
                <br>
                <span style='font-size:11px;color:#909599;'>上次更新：{{scope.row.last_modified_time}}</span>
            </span>
            <span v-else-if="scope.row.status=='ERROR'">
                <span style="color:#ff5a5f">●</span>
                {{audience_status_list[scope.row.status]['name']}}
                <br>
                <span style='font-size:11px;color:#909599;'>文件人群创建错误</span>
            </span>
            <span v-else>
                <span style="color:#ffbe00">●</span>
                {{audience_status_list[scope.row.status]['name']}}
                <br>
                <span style='font-size:11px;color:#909599;'>正在创建人群</span>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="user_count" label="可用人数" sortable :formatter="unitConversion">
        </el-table-column>
        <el-table-column prop="created_time" label="创建时间" sortable>
        </el-table-column>
      </el-table>
    </div>
    
    <el-dialog title="创建人群" :visible.sync="newAudienceVisible" width="830px" :modal="false" center>
    <div class="modal-header" >
      <p>已用人群配额/可用人群配额：{{audience_used}}/50 
        <el-tooltip placement="bottom" effect="light">
          <div slot="content">
            <p>人群配额为当前账户可用于创建人群的个数，配额已满时</p>
            <p>不支持创建新的人群，删除多余的人群可空出配额</p>
            <br/>
            <hr>
            <br/>
            <p>人群配额：共50个</p>
            <p>已用人群配额：共{{audience_used}}个</p>
            <p>可用人群配额：共{{audience_usable}}个</p>
          </div>
          <el-button type="text"><i class="el-icon-question"></i></el-button>
        </el-tooltip>
      </p>
    </div>
    <div class="field-list">
        <div>
        <div class="field-tit">
          私有人群 
          <el-tooltip placement="right" effect="light">
            <div slot="content">
              <span>智能拓展</span><i class="el-icon-check" style="color:green;padding-left:15px;"></i> 支持<br/>
              <span>组合</span><i class="el-icon-check" style="color:green;padding-left:39px;"></i> 支持<br/>
              <span>广告投放</span><i class="el-icon-check" style="color:green;padding-left:15px;"></i> 支持<br/>
              <span>洞察分析</span><i class="el-icon-check" style="color:green;padding-left:15px;"></i> 支持<br/>
              <span>共享人群</span><i class="el-icon-check" style="color:green;padding-left:15px;"></i> 支持<br/>
            </div>
            <el-button type="text"><i class="el-icon-info"></i></el-button>
          </el-tooltip>
        </div>
        <div class="field-content">
            <div class="media-group">
                <a @click="newAudience('file')">
                <div class="media">
                    <div class="media-left">
                    <button class="btn btn-app btn-file"><span class="spaui-icon lg icons-lg-file"></span></button>
                    </div>
                    <div class="media-body">
                    <h4 class="media-heading">号码文件</h4>
                    <p>通过文件上传来创建人群</p>
                    </div>
                </div>
                </a>
            </div>
            <div class="media-group">
                <a @click="newAudience('ad')">
                <div class="media">
                    <div class="media-left">
                    <button class="btn btn-app btn-file" style="background-color: #3ecbd7;">
                        <span class="spaui-icon lg icons-lg-file" style="background-position: -168px -488px;"></span>
                    </button>
                    </div>
                    <div class="media-body">
                    <h4 class="media-heading">广告受众</h4>
                    <p>通过历史广告曝光/点击创建人群</p>
                    </div>
                </div>
                </a>
            </div>
        </div>
        </div>
    </div>
    <br/>
    <div class="field-list">
        <div>
        <div class="field-tit">
          拓展人群 
          <el-tooltip placement="right" effect="light">
            <div slot="content">
              <span>广告投放</span><i class="el-icon-check" style="color:green;padding-left:15px;"></i> 支持<br/>
              <span>共享人群</span><i class="el-icon-check" style="color:green;padding-left:15px;"></i> 支持<br/>
            </div>
            <el-button type="text"><i class="el-icon-info"></i></el-button>
          </el-tooltip>
        </div>
        <div class="field-content">
            <div class="media-group">
                <a @click="newAudience('lookalike')">
                <div class="media">
                    <div class="media-left">
                    <button class="btn btn-app btn-file" style="background-color: #4d4e82;">
                      <span class="spaui-icon lg icons-lg-file" style="background-position: -510px -112px;"></span>
                    </button>
                    </div>
                    <div class="media-body">
                    <h4 class="media-heading">智能拓展</h4>
                    <p>根据人群特征智能计算出更多相似的新人群</p>
                    </div>
                </div>
                </a>
            </div>
        </div>
        </div>
    </div>
    </el-dialog>
  </div>

</template>

<script>
export default {
  created: function() {
    this.getEnumList()
    if (this.account_id) {
      this.getList()
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getList()
    },
    // 筛选
    options_list() {
      this.optionsChange()
    },
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    },
    options_list() {
        var options = JSON.stringify(this.optionsValue)
        return options
    }
  },
  data() {
    return {
      audience_used:0,
      audience_usable:50,
      tableData: [],
      tableDataConfig: [],
      audience_type_list: {},
      audience_status_list: {},
      loading : false,
      newAudienceVisible: false,
      optionsValue: { 
          type:[],
          status:[],
          source:[],
          name:''
      },
      options: {
          type: [{
            value: '私有人群',
            label: '私有人群'
          }, {
            value: '拓展人群',
            label: '拓展人群'
          }, {
            value: '组合人群',
            label: '组合人群'
        }], 
        status: [{
            value: 'PENDING',
            label: '待处理'
          }, {
            value: 'PROCESSING',
            label: '处理中'
          }, {
            value: 'SUCCESS',
            label: '可用'
          }, {
            value: 'ERROR',
            label: '错误'
        }], 
        source: [{
            value: 'CUSTOMER_FILE',
            label: '号码文件'
          }, {
            value: 'AD',
            label: '广告受众'
          }, {
            value: 'USER_ACTION',
            label: '用户行为'
          }, {
            value: 'LBS',
            label: '地理位置'
          }, {
            value: 'KEYWORD',
            label: '关键词'
          }, {
            value: 'COMBINE',
            label: '交并差组合'
          }, {
            value: 'LOOKALIKE',
            label: '智能拓展'
        }]
      },
      multipleSelection: [], // 批量 rows
    }
  },
  methods: {
    // 新建
    newAudience(val) {
      console.log(val)
      this.$router.push('/setting/audience/'+val)
    },

    optionsChange(){
        this.tableData = []
        for (var key in this.tableDataConfig) {
            // 过滤类型
            if(this.optionsValue.type.length != 0){
                if(this.optionsValue.type.indexOf(this.audience_type_list[this.tableDataConfig[key]['type']]['parent_name']) == -1){
                    continue
                }
            }
            // 过滤状态
            if(this.optionsValue.status.length != 0){
                if(this.optionsValue.status.indexOf(this.tableDataConfig[key]['status'])  == -1){
                    continue
                }
            }
            // 过滤来源
            if(this.optionsValue.source.length != 0){
                if(this.optionsValue.source.indexOf(this.tableDataConfig[key]['type'])  == -1){
                    continue
                }
            }
            // 模糊匹配名称
            if(this.tableDataConfig[key]['label'].match(this.optionsValue.name) === null ) {
                continue
            }
            this.tableData.push(this.tableDataConfig[key])
        }
    },

    // 获取人群列表
    getList() {
      this.loading = true 
      let params = {}
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_CustomAudiences/get', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.tableData = that.tableDataConfig = respData.data
            that.audience_used = respData.data.length
            that.audience_usable = 50 - that.audience_used
            that.optionsChange()
            that.loading = false 
          } else {
            that.loading = false 
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
            that.loading = false 
            that.$message('网络繁忙，请稍后再试！')
            console.log(err)
        })
    },

    sortTypeChange(a,b) {
        if(a.type > b.type){
            return true
        }else{
            return false
        }
    },

    sortStatusChange(a,b) {
        if(a.status > b.status){
            return true
        }else{
            return false
        }
    },

    unitConversion(row, column) {
      var val = row[column.property]
      val = (val + '').replace(/(?=(?!(\b))(\d{3})+$)/g, ",")
      return val
    },

    // 获取类型映射枚举列表
    getEnumList() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.audience_type_list = respData.data['AUDIENCE_TYPE']
            that.audience_status_list = respData.data['AUDIENCE_STATUS']
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    
    //  批量 rows
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 刷新人群
    handleListRefresh() {
      this.getList()
    },
    // 批量删除人群
    handleListRemove() {
      var that = this
      var rows = this.multipleSelection
      if (rows && rows.length > 0) {
        if(rows.length == 1){
            var ids = []
            for (var i in rows) {
                ids.push(rows[i]['id'])
            }
            
            this.$confirm('确认删除记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
            .then(() => {
                var params = {
                ids: ids
                }
                that.audiencesBatchDelete(params) // 批量删除请求
            })
            .catch(() => {})
        }else{
            that.$message('不支持批量操作')
        }
      } else {
        that.$message('请选择需要删除的记录')
      }
    },
    
    // 批量删除
    audiencesBatchDelete(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_CustomAudiences/batchDelete', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.getList() // 刷新人群列表
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
  },
}
</script>


<style scoped>
.modal-header {
  padding-bottom: 10px;
  text-align: center;
}
.field-list {
    padding: 0;
    margin: 0;
    overflow: auto;
}
/* style="background-color: #D3DCE6;color: #333;text-align: center;float:left;width:35%;" */
.field-list .field-tit {
    float:left;
    width: 150px;
    height: 110px;
    line-height: 110px;
    /* padding-left: 40px; */
    border-right: 1px solid #e3e4e5;
    vertical-align: middle;
    /* padding-right: 30px; */
    text-align: center;
    display: table-cell;
}
.field-list .field-content {
    float:left;
    width: 590px;
    padding: 0 10px 0 20px;
}
.field-list .field-content .media-group {
    width: 50%;
    float: left;
}
.field-list .field-content .media-group a {
    color: #2a2f33!important;
    display:block;
    cursor:pointer;
}
.field-list .field-content .media-group a:hover {
    background-color:#d0e9fa;
}
.field-content .media-group .media {
    padding: 10px;
    margin: 5px 10px 5px 0;
    height: 76px;
    line-height: 75px;
}
.field-content .media-group .media-left {
    float: left;
    width: 75px;
}
.btn-app.btn-file, .btn-app.btn-file:focus {
    background-color: #1690e5;
    /* border-color: #1690e5; */
    color: #fff;
}
.btn {
    display: inline-block;
    margin-bottom: 0;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.btn-app {
    height: auto;
    padding: 12px;
    font-size: 0;
    line-height: 0;
}
.field-content .media-group .media-body {
    width: 175px;
    float: left;
    vertical-align: middle;
    line-height: 1;
    text-align: left;
    padding-top: 15px;
}
.media-group .media-body .media-heading {
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 10px;
}
.media-group .media-body p {
    font-size: 12px;
    color: #909599;
}
.spaui-icon.lg {
    width: 30px;
    height: 30px;
}
.icons-lg-file {
	background-image: url(../../assets/lib-export.png);
}
.icons-lg-file {
    background-position: -131px -500px;
}
.spaui-icon {
    margin-top: 0;
}
.spaui-icon {
    display: inline-block;
    margin: 0 5px;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    font-family: spaui-icon!important;
    font-size: 0;
    font-style: normal;
    font-weight: 400!important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-smoothing: antialiased;
    vertical-align: middle;
    line-height: 1;
}
</style>



